import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue, SelectGroup, SelectLabel } from '@/components/ui/select';
import { Checkbox } from '@/components/ui/checkbox';
import { ArrowLeft, MapPin, Phone, Mail, User } from 'lucide-react';
import { Link } from 'react-router-dom';
import ImageUpload from '@/components/ImageUpload';
import ChatBot from '@/components/ChatBot';
import { Item, AIClassification } from '@/types';
import { useAuth } from '@/context/AuthContext';
import { toast } from 'sonner';

const CATEGORIES = {
  "Electronics": [
    { value: "mobile_phone", label: "Mobile Phone" },
    { value: "laptop", label: "Laptop" },
    { value: "tablet", label: "Tablet" },
    { value: "headphones", label: "Headphones / Earbuds" },
    { value: "camera", label: "Camera" },
    { value: "smart_watch", label: "Smart Watch" },
    { value: "charger", label: "Charger / Cable" }
  ],
  "Personal Accessories": [
    { value: "wallet", label: "Wallet / Purse" },
    { value: "bag", label: "Bag / Backpack" },
    { value: "keys", label: "Keys" },
    { value: "glasses", label: "Glasses / Sunglasses" },
    { value: "jewelry", label: "Jewelry" },
    { value: "watch", label: "Watch (Analog/Digital)" }
  ],
  "Clothing": [
    { value: "upper_wear", label: "Upper Wear (Shirt, Jacket, etc.)" },
    { value: "lower_wear", label: "Lower Wear (Pants, Skirt, etc.)" },
    { value: "footwear", label: "Footwear" },
    { value: "headwear", label: "Headwear (Hat, Cap)" }
  ],
  "Documents": [
    { value: "id_card", label: "ID Card / Driver's License" },
    { value: "passport", label: "Passport" },
    { value: "student_id", label: "Student ID" },
    { value: "credit_card", label: "Credit / Debit Card" }
  ],
  "Academic": [
    { value: "book", label: "Book / Notebook" },
    { value: "stationery", label: "Stationery" }
  ],
  "Other": [
    { value: "umbrella", label: "Umbrella" },
    { value: "water_bottle", label: "Water Bottle" },
    { value: "medical", label: "Medical Items" },
    { value: "sports", label: "Sports Equipment" },
    { value: "other", label: "Other" }
  ]
};

const CATEGORY_MAPPING: Record<string, string> = {
  'phone': 'mobile_phone',
  'smart phone': 'mobile_phone',
  'cell phone': 'mobile_phone',
  'wallet': 'wallet',
  'keys': 'keys',
  'bag': 'bag',
  'backpack': 'bag',
  'laptop': 'laptop',
  'computer': 'laptop',
  'headphones': 'headphones',
  'earphones': 'headphones',
  'jewelry': 'jewelry',
  'clothing': 'upper_wear', // Default assumption
  'books': 'book',
  'umbrella': 'umbrella',
  'glasses': 'glasses',
  'sunglasses': 'glasses',
  'id_card': 'id_card',
  'watch': 'watch'
};

const LOCATIONS = [
  'Library - Main Floor',
  'Library - 2nd Floor',
  'Library - Study Rooms',
  'Student Center - Cafeteria',
  'Student Center - Lounge',
  'Classroom Building A',
  'Classroom Building B',
  'Gymnasium',
  'Parking Lot A',
  'Parking Lot B',
  'Dormitory - Common Area',
  'Other'
];

export default function ReportLost() {
  const navigate = useNavigate();
  const { user, isAuthenticated, isLoading } = useAuth();
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    category: '',
    location: '',
    customLocation: '',
    contactName: user?.name || '',
    contactEmail: user?.email || '',
    contactPhone: '',
    tags: '',
    verificationRequired: true
  });
  const [imageUrl, setImageUrl] = useState<string>('');
  const [uploadedFile, setUploadedFile] = useState<File | null>(null);
  const [aiClassification, setAiClassification] = useState<AIClassification | null>(null);
  const [isSubmitting, setIsSubmitting] = useState(false);

  // Auth check removed to allow public access

  if (isLoading) {
    return <div className="min-h-screen flex items-center justify-center">Loading...</div>;
  }

  const handleInputChange = (field: string, value: string | boolean) => {
    setFormData(prev => ({ ...prev, [field]: value }));
  };

  const handleChatSuggestion = (suggestion: string) => {
    const lower = suggestion.toLowerCase();

    // Auto-fill category based on suggestion
    for (const [key, value] of Object.entries(CATEGORY_MAPPING)) {
      if (lower.includes(key)) {
        setFormData(prev => ({ ...prev, category: value }));
        break;
      }
    }

    // Auto-fill description if it's empty
    if (!formData.description && suggestion.length > 10) {
      setFormData(prev => ({ ...prev, description: suggestion }));
    }
  };

  const handleImageClassification = (classification: AIClassification) => {
    setAiClassification(classification);
    // Auto-fill category if not already set
    if (!formData.category && classification.category) {
      const mappedCategory = CATEGORY_MAPPING[classification.category.toLowerCase()] || classification.category;
      // Check if mapped category exists in our grouped options (simple validation)
      let isValidCategory = false;
      Object.values(CATEGORIES).forEach(group => {
        if (group.some(item => item.value === mappedCategory)) isValidCategory = true;
      });

      if (isValidCategory) {
        setFormData(prev => ({ ...prev, category: mappedCategory }));
      } else {
        // Fallback or keep as is if it matches a valid value directly
        // check if the raw classification is a valid value
        let isRawValid = false;
        Object.values(CATEGORIES).forEach(group => {
          if (group.some(item => item.value === classification.category)) isRawValid = true;
        });

        if (isRawValid) {
          setFormData(prev => ({ ...prev, category: classification.category }));
        }
      }
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);

    try {
      const finalLocation = formData.location === 'Other' ? formData.customLocation : formData.location;
      const tags = formData.tags.split(',').map(tag => tag.trim()).filter(Boolean);

      // Prepare FormData for backend
      const formDataToSend = new FormData();
      formDataToSend.append('title', formData.title);
      formDataToSend.append('description', formData.description);
      formDataToSend.append('category', formData.category);
      formDataToSend.append('location', finalLocation);
      formDataToSend.append('date_lost', new Date().toISOString());
      formDataToSend.append('user_id', 'guest');
      formDataToSend.append('contact_email', formData.contactEmail);
      formDataToSend.append('contact_phone', formData.contactPhone);
      formDataToSend.append('tags', JSON.stringify(tags));

      // Add AI metadata if available
      if (aiClassification) {
        formDataToSend.append('ai_metadata', JSON.stringify(aiClassification));
        // Explicitly append embedding if available
        if (aiClassification.embedding) {
          formDataToSend.append('embedding', JSON.stringify(aiClassification.embedding));
        }
      }

      // Add image if uploaded
      if (uploadedFile) {
        formDataToSend.append('image', uploadedFile);
      }

      // Call backend API
      const apiResponse = await fetch('/api/items/lost', {
        method: 'POST',
        body: formDataToSend,
      });

      if (!apiResponse.ok) {
        const errorText = await apiResponse.text();
        console.error('API Error Response:', errorText);
        throw new Error(`Failed to create item: ${apiResponse.status} - ${errorText}`);
      }

      const createdItem = await apiResponse.json();

      toast.success('Lost item reported successfully!');

      // Check for matches and redirect if found
      if (createdItem.hasMatches && createdItem.matches && createdItem.matches.length > 0) {
        const firstMatchId = createdItem.matches[0].item.dbId; // Use dbId (integer) or id (string w prefix)?
        // The item.id in matches is "found_123" or similar.
        // We want to redirect to that item.
        const matchId = createdItem.matches[0].item.id;
        toast.info(`Found ${createdItem.matchCount} potential matches! Redirecting...`);
        // Add a small delay so user sees the toast
        setTimeout(() => {
          navigate(`/item/${matchId}?match_source=${createdItem.id}`);
        }, 1500);
      } else {
        navigate(`/item/${createdItem.id}?success=true`);
      }
    } catch (error) {
      console.error('Error saving item:', error);
      toast.error('Failed to report item. Please try again.');
    } finally {
      setIsSubmitting(false);
    }
  };

  const isFormValid = formData.title && formData.description && formData.category &&
    (formData.location !== 'Other' ? formData.location : formData.customLocation) &&
    formData.contactName && formData.contactEmail;

  return (
    <div className="min-h-screen bg-gray-50">
      <header className="bg-white shadow-sm border-b">
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center h-16">
            <Button variant="ghost" size="sm" asChild>
              <Link to="/">
                <ArrowLeft className="h-4 w-4 mr-2" />
                Back to Home
              </Link>
            </Button>
            <h1 className="ml-4 text-xl font-semibold text-gray-900">Report Lost Item</h1>
          </div>
        </div>
      </header>

      <main className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Main Form */}
          <div className="lg:col-span-2">
            <Card>
              <CardHeader>
                <CardTitle>Lost Item Details</CardTitle>
              </CardHeader>
              <CardContent>
                <form onSubmit={handleSubmit} className="space-y-6">
                  {/* Image Upload */}
                  <div>
                    <Label className="text-base font-medium">Item Photo (Optional but Recommended)</Label>
                    <p className="text-sm text-gray-500 mb-3">
                      Upload a photo for AI-powered matching. Our system will automatically analyze the image.
                    </p>
                    <ImageUpload
                      onImageSelect={(url, _, file) => {
                        setImageUrl(url);
                        if (file) {
                          setUploadedFile(file);
                        }
                      }}
                      onClassificationComplete={handleImageClassification}
                      previewUrl={imageUrl}
                    />
                  </div>

                  {/* Basic Information */}
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="title">Item Title *</Label>
                      <Input
                        id="title"
                        value={formData.title}
                        onChange={(e) => handleInputChange('title', e.target.value)}
                        placeholder="e.g., Black iPhone 14"
                        required
                      />
                    </div>

                    <div>
                      <Label htmlFor="category">Category *</Label>
                      <Select value={formData.category} onValueChange={(value) => handleInputChange('category', value)}>
                        <SelectTrigger>
                          <SelectValue placeholder="Select category" />
                        </SelectTrigger>
                        <SelectContent>
                          {Object.entries(CATEGORIES).map(([groupName, items]) => (
                            <SelectGroup key={groupName}>
                              <SelectLabel>{groupName}</SelectLabel>
                              {items.map((item) => (
                                <SelectItem key={item.value} value={item.value}>
                                  {item.label}
                                </SelectItem>
                              ))}
                            </SelectGroup>
                          ))}
                        </SelectContent>
                      </Select>
                    </div>
                  </div>

                  <div>
                    <Label htmlFor="description">Detailed Description *</Label>
                    <Textarea
                      id="description"
                      value={formData.description}
                      onChange={(e) => handleInputChange('description', e.target.value)}
                      placeholder="Describe your item in detail: color, brand, size, distinctive features, condition, etc."
                      rows={4}
                      required
                    />
                  </div>

                  {/* Location */}
                  <div>
                    <Label htmlFor="location">Last Known Location *</Label>
                    <Select value={formData.location} onValueChange={(value) => handleInputChange('location', value)}>
                      <SelectTrigger>
                        <SelectValue placeholder="Where did you last see it?" />
                      </SelectTrigger>
                      <SelectContent>
                        {LOCATIONS.map((location) => (
                          <SelectItem key={location} value={location}>
                            <div className="flex items-center gap-2">
                              <MapPin className="h-4 w-4" />
                              {location}
                            </div>
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>

                    {formData.location === 'Other' && (
                      <Input
                        className="mt-2"
                        value={formData.customLocation}
                        onChange={(e) => handleInputChange('customLocation', e.target.value)}
                        placeholder="Please specify the location"
                        required
                      />
                    )}
                  </div>

                  <div>
                    <Label htmlFor="tags">Tags (Optional)</Label>
                    <Input
                      id="tags"
                      value={formData.tags}
                      onChange={(e) => handleInputChange('tags', e.target.value)}
                      placeholder="e.g., cracked screen, blue case, small scratch (separate with commas)"
                    />
                  </div>

                  {/* Contact Information */}
                  <div className="space-y-4">
                    <h3 className="text-lg font-medium">Contact Information</h3>

                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      <div>
                        <Label htmlFor="contactName">Your Name *</Label>
                        <div className="relative">
                          <User className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-4 w-4" />
                          <Input
                            id="contactName"
                            value={formData.contactName}
                            onChange={(e) => handleInputChange('contactName', e.target.value)}
                            placeholder="Full name"
                            className="pl-10"
                            required
                          />
                        </div>
                      </div>

                      <div>
                        <Label htmlFor="contactEmail">Email Address *</Label>
                        <div className="relative">
                          <Mail className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-4 w-4" />
                          <Input
                            id="contactEmail"
                            type="email"
                            value={formData.contactEmail}
                            onChange={(e) => handleInputChange('contactEmail', e.target.value)}
                            placeholder="your.email@example.com"
                            className="pl-10"
                            required
                          />
                        </div>
                      </div>
                    </div>

                    <div>
                      <Label htmlFor="contactPhone">Phone Number (Optional)</Label>
                      <div className="relative">
                        <Phone className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-4 w-4" />
                        <Input
                          id="contactPhone"
                          type="tel"
                          value={formData.contactPhone}
                          onChange={(e) => handleInputChange('contactPhone', e.target.value)}
                          placeholder="+1 (555) 123-4567"
                          className="pl-10"
                        />
                      </div>
                    </div>
                  </div>

                  {/* Verification */}
                  <div className="flex items-center space-x-2">
                    <Checkbox
                      id="verification"
                      checked={formData.verificationRequired}
                      onCheckedChange={(checked) => handleInputChange('verificationRequired', checked as boolean)}
                    />
                    <Label htmlFor="verification" className="text-sm">
                      Require verification before claiming (recommended for valuable items)
                    </Label>
                  </div>

                  <Button
                    type="submit"
                    className="w-full"
                    disabled={!isFormValid || isSubmitting}
                  >
                    {isSubmitting ? 'Submitting...' : 'Report Lost Item'}
                  </Button>
                </form>
              </CardContent>
            </Card>
          </div>

          {/* AI Assistant Sidebar */}
          <div>
            <ChatBot
              className="h-full border-none shadow-none"
              onSuggestionApply={handleChatSuggestion}
              onInfoExtracted={(info) => {
                if (info.category) {
                  const mappedCategory = CATEGORY_MAPPING[info.category.toLowerCase()] || info.category.toLowerCase();
                  setFormData(prev => ({ ...prev, category: mappedCategory }));
                }
                if (info.description) {
                  setFormData(prev => ({ ...prev, description: info.description }));
                }
                if (info.location) {
                  const isKnownLocation = LOCATIONS.includes(info.location);
                  if (isKnownLocation) {
                    setFormData(prev => ({ ...prev, location: info.location }));
                  } else {
                    setFormData(prev => ({ ...prev, location: 'Other', customLocation: info.location }));
                  }
                }
              }}
            />
          </div>
        </div>
      </main>
    </div>
  );
}